@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Alex+Brush&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0px 15px;
}

/*-----------------------top nav ---------------------------*/

.background_container {
  width: 100%;
  height: 100vh;
  background: url(../img/pexels-david-besh-884788.jpg);
  background-position: center;
  background-size: cover;
  z-index: -1;
}

.top_nav {
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(26, 26, 26, 0.5333333333);
}

.top_nav .tel_number a {
  text-decoration: none;
  color: white;
}

.top_nav a {
  text-decoration: none;
}

.top_nav a svg {
  width: 50px;
}

.top_nav .icons {
  padding-left: 20px;
}

.top_nav .tel_number {
  padding-right: 20px;
}

nav {
  width: 100%;
  height: 70px;
  background-color: rgba(26, 26, 26, 0.5333333333);
  position: sticky;
}

nav .logo a {
  font-weight: bold;
  font-size: 35px;
  font-family: "Alex Brush", cursive;
  color: #fff;
}

nav .nav-links {
  justify-content: space-between;
  width: 100%;
  height: 70px;
  display: flex;
  align-items: center;
}

nav .nav-links ul {
  font-family: NSARegular, sans-serif;
  font-weight: bold;
  display: flex;
}

nav .nav-links ul li {
  list-style: none;
  text-align: center;
  padding: 25px;
  cursor: pointer;
  transition: 0.5s;
}

nav .nav-links ul li a {
  text-decoration: none;
  color: #fff;
}

nav .nav-links ul li:hover {
  background-color: rgba(134, 134, 134, 0.263);
  color: #e3774c;
}

nav .nav-links ul li .dropbtn {
  color: white;
  font-size: 16px;
  border: none;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0);
}

nav .nav-links ul li .dropdown {
  position: relative;
  display: inline-block;
}

nav .nav-links ul li .dropdown-content {
  display: none;
  position: absolute;
  background-color: rgba(249, 249, 249, 0);
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

nav .nav-links ul li .dropdown-content a {
  color: rgb(255, 255, 255);
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

nav .nav-links ul li .dropdown-content a:hover {
  background-color: rgba(94, 94, 94, 0.431372549);
}

nav .nav-links ul li .dropdown:hover .dropdown-content {
  display: block;
}

