}

main {
  height: 100vh;
  background-image: url(../img/pexels-mnm-zameer-3308588.jpg);
  background-position: center;
  background-size: cover;
}

main .first_site {
  display: flex;
}

main .first_site .img_block img {
  width: 400px;
  height: 220px;
  margin-top: 70px;
  border-radius: 10px;
}

main .first_site .description h1 {
  font-size: 40px;
  font-weight: 700;
  color: rgb(255, 102, 0);
  padding-top: 80px;
  padding-left: 50px;
  letter-spacing: 3px;
}

main .first_site .description p {
  font-size: 30px;
  font-weight: 700;
  color: rgb(228, 188, 161);
  letter-spacing: 3px;
  padding-left: 50px;
}

header {
  width: 100%;
  height: 80%;
  background-color: rgba(16, 16, 16, 0.3568627451);
}

header .scrollDown {
  width: 100%;
  position: relative;
  text-align: center;
  z-index: 55;
  cursor: pointer;
  top: 90px;
  animation: chgitem 0.6s ease alternate infinite;
}

header .scrollDown svg {
  width: 70px;
  height: 70px;
  fill: #fff;
}

@keyframes chgitem {
  from {
    top: 120px;
  }

  to {
    top: 90px;
  }
}

header .avatar_container {
  display: flex;
  position: absolute;
  width: 300px;
  height: 350px;
  background: #fff;
  margin-top: 50px;
  background-image: url(../img/avatar.png);
  background-position: left bottom;
  border-radius: 0px 80px 0px 80px;
  opacity: 80%;
  background-size: 400px;
}

header .description_container {
  position: relative;
  display: flex;
}

header .description_container h1 {
  padding-top: 100px;
  padding-left: 350px;
  letter-spacing: 2px;
  font-size: 55px;
  color: rgb(255, 255, 255);
}

